import {
  LOGING_FALUIRE,
  LOGIN_REQUEST,
  LOGIN_SUCCESS,
  GET_ACCESS_TOKEN,
  SET_STATES_FALSE,
} from './types';

const initialState = {
  user: false,
  requesting: false,
  error: false,
  accessToken: false,
};

export default (state = initialState, action) => {
  switch (action.type) {
    case LOGIN_REQUEST:
      return {
        ...state,
        requesting: true,
      };

    case LOGIN_SUCCESS:
      return {
        ...state,
        requesting: false,
        user: action.data,
      };

    case LOGING_FALUIRE:
      return {
        ...state,
        requesting: false,
        error: action.data,
      };

    case GET_ACCESS_TOKEN:
      return {
        ...state,
        requesting: false,
        accessToken: action.data,
      };
    case SET_STATES_FALSE:
      return {
        ...state,
        user: false,
        requesting: false,
        error: false,
        accessToken: false,
      };

    default:
      return state;
  }
};
